<template>
  <div v-if="user" class="user">
    <div style="width:100%">
      <Panel>
        <template #header>
          <router-link to="/">主页</router-link>
        </template>
        <template #content>
          <div class="content">
            <div>
              <el-avatar class="avatar" size="medium" shape="square" :src="user.avatar_url"></el-avatar>
              <span>{{user.loginname}}</span>
            </div>
            <div>{{user.score}}积分</div>
            <div>
              github
              <a target="_blank" :href="`https://github.com/${user.githbuUsername}`"></a>
              @{{user.githubUsername}}
            </div>
            <div>注册时间{{user.create_at|moment('from','now')}}</div>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #header>最近创建</template>
        <template #content>
          <PostItem v-for="topic in recent_topics" :key="topic.id" v-bind="topic" />
        </template>
      </Panel>
      <Panel>
        <template #header>最近参与</template>
        <template #content>
          <PostItem v-for="topic in recent_replies" :key="topic.id" v-bind="topic" />
        </template>
      </Panel>
    </div>
    <AsideInfo />
  </div>
</template>

<script>
import axios from "../plugins/axios";
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import PostItem from "../components/PostItem.vue";
import AsideInfo from "../components/AsideInfo.vue";
export default {
  components: { Panel, PostItem, AsideInfo },
  data() {
    return {
      recent_replies: [],
      recent_topics: []
    };
  },
  computed: {
    ...mapState({
      user: state => state.user.user
    })
  },
  async created() {
    await this.getUser(this.$route.params.username);
    // console.log(this.user)
  },
  watch: {
    "$route.params.username": {
      handler(newValue) {
        this.getUser(newValue);
      },
      immediate: true
    },
    user: {
      // 监听user变化  1.首次进入user页，默认user是null然后action触发之后user更新了 2. 在当前页切换其他用户
      async handler(newValue) {
        // 请求组要发多个
        const res1 = await axios.all(
          newValue.recent_replies.map(topic => axios.get(`/topic/${topic.id}`))
        );
        this.recent_replies = res1.map(ele => ele.data);

        const res2 = await axios.all(
          newValue.recent_topics.map(topic => axios.get(`/topic/${topic.id}`))
        );
        this.recent_topics = res2.map(ele => ele.data);
      }
    }
  },
  methods: {
    ...mapActions(["getUser"])
  }
};
</script>

<style lang="less">
.user{
  display: flex;
  .content {
    > div {
      margin-bottom: 20px;
    }
  }
}
</style>