<template>
  <header>
    <div class="header-inner">
      <h1>
        <router-link to="/">
          <img src="../assets/logo-1.png" alt />
        </router-link>
      </h1>
      <div class="header-right">
        <router-link to="/">首页</router-link>
        <el-badge :hidden="true" :value="messageCount" class="item" type="primary" style="margin-right:15px">
          <!-- <router-link v-if="info" to="/message" >未读消息</router-link> -->
          <a @click="markAll" v-if="info" >未读消息</a>
        </el-badge>
        <router-link v-if="!info" to="/login">登录</router-link>
        <div v-else>
          <router-link :to="`/user/${info.loginname}`">
            <el-avatar class="avatar" size="medium" shape="square" :src="info.avatar_url"></el-avatar>
          </router-link>
          <a @click="logout">退出</a>
        </div>
      </div>
    </div>
  </header>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
export default {
  data() {
    return {
      messageCount:null
    }
  },
  computed: {
    ...mapState({
      info: (state) => state.login.info,
    }),
  },
  methods: {
    ...mapMutations(['getInfo']),
    logout() {
      if(this.$route.path !== '/'){
        this.$router.push('/')
      }
      localStorage.removeItem('info')
      localStorage.removeItem('token')
      this.getInfo(null)
    },
    markAll(){
      if(this.messageCount){
        const accesstoken =  localStorage.getItem('token')
        this.$axios.post(`/message/mark_all`,{accesstoken})
      }
      this.$router.push('/message')
    }
  },
  created () {
    const accesstoken =  localStorage.getItem('token')
    // console.log(accesstoken)
    const res = this.$axios.get(`/message/count?accesstoken=${accesstoken}`);
    this.messageCount = res.data
    // console.log(this.messageCount)
  },
};
</script>

<style lang='less'>
header {
  width: 100%;
  height: 70px;
  background-color: #fff;
  box-shadow: 0 0 10px #ccc;
  position: fixed;
  z-index: 999;
  top: 0;
  display: flex;
    align-items: center;

  .header-inner {
    margin: 0 auto;
    max-width: 1400px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h1 {
      margin: 0;
      width: 140px;
      img{
        width: 100%;
      }
    }
    .header-right{
      display: flex;
      align-items: center;
      div{
        display: flex;
        align-items: center;
      }
      a{
        cursor: pointer;
        color: #666;
        margin: 0 15px;
        &:hover{
          color: #ff6000;
        }
      }
    }
  }
}
</style>