<template>
  <div class="aside-info" v-if="info || $route.params.username || postLoginname">
    <Aside v-if="user">
      <template #header>个人信息</template>
      <template #content>
        <div>
          <router-link :to=" `/user/${user.loginname}` ">
            <el-avatar size="medium" shape="square" :src="user.avatar_url"></el-avatar>
            <span>{{user.loginname}}</span>
          </router-link>
        </div>
        <p>{{user.score}}积分</p>
      </template>
    </Aside>
    <Aside v-if="user">
    <!-- <Aside> -->
      <template #content2>
        <el-button v-if="user.loginname===info.loginname" @click="addTopic" size="midle" type="primary">发布话题</el-button>
      </template>
    </Aside>
  </div>
  <div class="aside-info" v-else>
    <Aside>
      <template #content2>
        <p>HiNode专业中文社区</p>
        <el-button v-if="!info" type="primary" @click="$router.push('/login')">登录</el-button>
      </template>
    </Aside>
  </div>
</template>

<script>
import Aside from "../containers/Aside.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Aside
  },
  props: {
    postLoginname: {
      type: String
    }
  },
  computed: {
    ...mapState({
      user: state => state.user.user,
      info:state =>state.login.info
    }),
    // info() {
    //   return JSON.parse(localStorage.getItem("info"));
    // }
  },
  watch: {
    $route: {
      handler(to) {
        // console.log(to);
        if (this.info || this.$route.params.username || this.postLoginname) {
          const username = this.$route.params.username? this.$route.params.username: this.postLoginname? this.postLoginname: this.info.loginname;
          this.getUser(username);
        }
      },
      immediate: true
    }
  },
  created() {
    

    // if (this.info || this.$route.params.username || this.postLoginname) {
    //       const username = this.$route.params.username
    //         ? this.$route.params.username
    //         : this.postLoginname
    //         ? this.postLoginname
    //         : this.info.loginname;
    //       this.getUser(username);
    //       console.log(this.user);
    //       console.log(this.info);
    //     }
  },
  methods: {
    ...mapActions(["getUser"]),
    addTopic() {
      this.$router.push("/topic/create");
    }
  }
};
</script>

<style lang="less">
.aside-info {
  .aside {
    flex-shrink: 0;
    .aside-content {
      display: flex;
      flex-direction: column;
      align-items: start;
      .el-avatar {
        margin-right: 10px;
      }
      a {
        color: #666;
      }
    }
  }
}
</style>