import axios from "../../plugins/axios"

export default {
  state:()=>({
    list:[],
  }),
  mutations:{
    // 获取最初的文章
    getPosts(state,posts){
      state.list = posts
    },
    // 加载新的文章
    loadMorePosts(state,list){
      // 合并（数组和数组不能push）
      state.list = [...state.list,...list]
    }
  },
  actions:{
    // 请求文章列表有两种情况 1.初始请求 2.加载更多请求
    // 依据传参有无page，有
    async getPosts({commit},query){
      const {tab,page} = query
      const queryStr = page?`?tab=${tab}&page=${page}&limit=20`:`?tab=${tab}`
      const res = await axios.get(`/topics${queryStr}`)
      // console.log(res)
      page? commit('loadMorePosts',res.data) :commit('getPosts',res.data)
      
    },

  }
}