import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import PostList from '../views/PostList.vue'
import User from '../views/User.vue'
import Err from '../views/Err.vue'
Vue.use(VueRouter)

const routes = [
  {
    path:'/login',
    component:()=>import('../views/Login.vue')
  },
  {
    path:'/err',
    name:'err',
    component:Err
  },

  {
    path: '/',
    name: 'Home',
    component: Home,
    children:[
      // {
      //   path:'*',
      //   component:Err
      // },
      {
        // 默认子路由
        path:'',
        component:PostList
      },
      {
        path:':tab',
        component:PostList,
      },
    ]
  },
  {
    path:'/message',
    name:'message',
    component:()=>import('../views/Message.vue')
  },
  {
    path:'/topic/create',
    component:()=>import('../views/Create.vue')
  },
  {
    path:'/post/:id/edit',
    name:'postEdit',
    component:()=>import('../views/Create.vue')
  },
  {
    // 路由匹配是由上至下的 
    path:'/post/:id',
    name:'postContent',
    props:true,
    component:()=>import('../views/PostContent.vue')
  },
  {
    path:'/user/:username',
    component:User
  },
  {
    path:'*',
    component:Err
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
