<template>
  <div class="post-item">
    <router-link :to=" `/user/${author.loginname}`">
      <el-avatar class="avatar" shape="square" size="medium" :src="author.avatar_url"></el-avatar>
    </router-link>
    <div class="count-wrap">
      <span class="reply-count">{{reply_count}}</span>
      <span>/</span>
      <span class="visit_count">{{visit_count}}</span>
    </div>

    <el-tag v-if="top" type="primary" class="tag" size="mini">置顶</el-tag>
    <el-tag v-else-if="good" class="tag" type="primary" size="mini">精华</el-tag>
    <el-tag
      v-else-if="$route.params.tab==='all'||''"
      class="tag"
      type="info"
      size="mini"
    >{{tab|transformTab}}</el-tag>
    <!-- <router-link class="content" :to="{name:'postContent',params:{id:id}}">{{title}}</router-link> -->
    <router-link class="content" :to="`/post/${id}`">{{title}}</router-link>
    <span class="last-raply">{{last_reply_at|moment('from','now')}}</span>
  </div>
</template>

<script>
export default {
  props: [
    "id",
    "author",
    "reply_count",
    "visit_count",
    "title",
    "tab",
    "top",
    "good",
    "last_reply_at"
  ],
  computed: {
    // isAllOrGoodPage() {
    //   return this.data
    // }
  }
};
</script>

<style lang="less">
.post-item {
  display: flex;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #eee;

  .tag {
    margin: 0 5px;
  }
  .content {
    flex-grow: 1;
    display: inline-block;

    margin: 0;
    color: #222;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    &:visited {
      color: #777;
    }
    &:hover {
      text-decoration: underline;
    }
  }
  .avatar,
  .count-wrap,
  .tag,
  .last-raply {
    flex-shrink: 0;
  }
