<template>
  <div v-show="btnFlag"  @click="backTop" class="back">回到顶部</div>
</template>

<script>
export default {
  data() {
    return {
      btnFlag: false
    };
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },

  methods: {
    backTop() {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      const that = this;
      let scrollTop = window.pageYoffset || document.documentElement.scrollTop || document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 150) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    }
  }
};
</script>

<style lang='less'>
.back {
  cursor: pointer;
  width: 36px;
  padding: 12px;
  background-color: #fff;
  color: #ff6000;
  font-weight: bold;
  position: fixed;
  right: 0px;
  bottom: 50px;
  border-radius: 8px 0 0 8px;
  &:hover{
    background-color: #ff6000;
    color: #fff;
  }
}
</style>